<template>
  <div>
    <!--begin::Notice-->
    <div
      class="alert alert-custom alert-white alert-shadow gutter-b mt-1"
      role="alert"
    >
      <div class="alert-icon alert-icon-top">
        <div></div>
        <!--end::Svg Icon-->
      </div>

      <div class="alert-text alert-text-center">
        <p style="margin-top: 15px">
          <span
            class="label label-inline label-pill label-primary label-rounded mr-2"
          >
            DESCRIPTION:
          </span>
          Why TextFlint, and how to flint the text.
        </p>
      </div>
    </div>
    <!--end::Notice-->

    <!--start Tabs -->
    <div class="card card-custom gutter-b">
      <b-tabs card>
        <b-tab title="Introduction" active>
          <h1>TextFlint Slides</h1>

          <h2>Basic</h2>

          <h3>What&#39;s TextFlint?</h3>

          <p>
            TextFlint is a multilingual robustness evaluation platform for
            natural language processing tasks, which unifies general text
            transformation, task-specific transformation, adversarial attack,
            sub-population, and their combinations to provide a comprehensive
            robustness analysis.
          </p>

          <p>
            Up to now, TextFlint has 20 general transformations, 8
            subpopulations and 60 task-specific transformations, as well as
            thousands of their combinations, covering 12 different NLP tasks,
            such as sentiment analysis, relation extraction, named-entity
            recognition and machine reading comprehension.
          </p>

          <h3>Installation</h3>

          <p>
            To install TextFlint is easy. We recommend installing TextFlint with
            <code>pip</code>. Note that it requires <code>python</code> version
            at least 3.7.
          </p>

          <h3>Quick Start</h3>

          <p>
            Now let&#39;s have a quick start. In this term, we will try to
            generate transformed data on sentiment analysis task.
          </p>

          <p>
            We can import the <code>Engine</code> class from
            <code>textflint</code> and instantiate it with parameter
            <code>SA</code>, which means the sentiment analysis task. To run
            this engine, we need to feed it some data samples and an output
            directory.
          </p>

          <p>
            The data samples can be a <code>dict</code>, or a file in
            <code>JSON</code> or <code>csv</code> format. Please note that the
            format of data samples is corresponded to specific task, that is,
            the contents of <code>x</code> and <code>y</code> might be slightly
            different due to different tasks. You can find the corresponding
            data formats in our docs.
          </p>

          <p>
            After running <code>engine</code>, files with transformed data
            samples will be generated in the output directory, as well as files
            with original data samples. <code>trans_Keyboard_2.json</code> means
            that it has 2 successfully transformed data samples by
            transformation <code>Keyboard</code>.
          </p>

          <p>
            Here are the data samples in <code>ori_Keyboard_2.json</code> and in
            <code>trans_Keyboard_2.json</code>. We can see that some characters
            have been changed by the simulation of keyboard input error.
          </p>

          <h2>Advanced</h2>

          <h3>Customize Your Output</h3>

          <p>
            Sometimes we don&#39;t want to generate all of these
            transformations, or just hope to customize our own output. For this,
            we can run <code>engine</code> with a customized
            <code>config</code>.
          </p>

          <p>
            We can import the <code>Config</code> class from
            <code>textflint</code>, and use the method <code>from_dict</code> to
            obtain a <code>config</code> from a <code>dict</code>. For example,
            we can restrict transformation methods to
            <code>Keyboard</code> only. Then we will get output files only
            related to <code>Keyborad</code> transformation. You can also
            customize more parameters referring to our docs.
          </p>

          <h3>FlintModel and Robustness Report</h3>

          <p>
            If you want to evaluate the robustness of your models with textflint
            toolkit, FlintModel is a necessary input, and textflint will
            automatically generate the robustness report. For this, You need to
            provide the corresponding tokenizer to process data samples, and
            your own model object to support the prediction.
          </p>

          <h3>Define Your Own Transformation</h3>

          <p>
            Besides using built-in transformations, you can also define your own
            transformation. This is a transformation example, the
            <code>BreadWordSwap</code>. Its function is simple, which is
            replacing random words of a text with the word <code>bread</code>.
            You can define more transformations like this for your own need.
          </p>
        </b-tab>
        <b-tab title="Videos&Slices" data-app="true">
          <div class="row ">
            <div class="col-md-12">
              <h2>Video Tutorials</h2>
            </div>
            <b-card title="How to use TextFlint" class="col-xl-3 align-items-stretch">
              <a
                href="#"
                @click.prevent="show_video('How to use TextFlint.mp4')"
              >
                <v-img
                  src="../../../public/media/logos/Videos/How_to_use_textflint.png"
                >
                </v-img>
              </a>
            </b-card>
          </div>
          <div class="row pt-10">
            <div class="col-md-12">
              <h2>Slice Tutorials</h2>
            </div>
            <b-card title="Quick Start" class="col-xl-3 align-items-stretch">
              <a href="#" @click.prevent="show_slides('Quick Start.ppt')">
                <v-img src="../../../public/media/logos/Slides/Quick Start.png">
                </v-img>
              </a>
            </b-card>
          </div>

          <v-dialog max-height="50%" max-weight="50%" v-model="dialog_video">
            >
            <v-card>
              <v-btn
                icon
                error
                @click="
                  dialog_video = false;
                  video_url = '';
                "
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
              <v-card-title>
                <span class="headline">{{ video_title }}</span>
              </v-card-title>
              <v-card-text>
                <b-embed
                  type="iframe"
                  aspect="16by9"
                  :src="video_url"
                  width="50%"
                  allowfullscreen
                ></b-embed>
              </v-card-text>
              <!-- <v-card-actions>
                <v-spacer>this video is used for xxx</v-spacer>
                <v-btn color="green darken-1" text @click="dialog_video = false"
                  >Close</v-btn
                >
              </v-card-actions> -->
            </v-card>
          </v-dialog>

          <v-dialog max-height="70%" max-weight="70%" v-model="dialog">
            >
            <v-card>
              <v-btn icon error @click="dialog = false">
                <v-icon>mdi-close</v-icon>
              </v-btn>
              <v-card-title>
                <span class="headline">{{ slides_title }}</span>
              </v-card-title>
              <v-card-text>
                <iframe :src="pathUrl" width="100%" height="800"></iframe>
              </v-card-text>
              <!-- <v-card-actions>
                <v-spacer>this slices is used for xxx</v-spacer>
                <v-btn color="green darken-1" text @click="dialog = false"
                  >Close</v-btn
                >
              </v-card-actions> -->
            </v-card>
          </v-dialog>
        </b-tab>
        <b-tab title="Interactive Demo">
          <div class="row font-size-data">
            <b-card title="" class="col-xl-3 bg-light">
              <b-form @submit="onSubmit" @reset="onReset" v-if="show">
                <b-form-group
                  id="input-group-Task"
                  label="Task"
                  label-class="font-size-label font-weight-bold"
                  label-for="input-task"
                >
                  <b-form-select
                    id="input-task"
                    v-model="form.task"
                    :options="tasks"
                    @change="getTransformation()"
                    required
                  ></b-form-select>
                </b-form-group>

                <b-form-group
                  id="input-group-transformation"
                  label="Transformation"
                  label-class="font-size-label font-weight-bold"
                  label-for="input-transformation"
                >
                  <b-form-select
                    id="input-transformation"
                    v-model="form.transformation"
                    :options="transformations"
                    required
                  ></b-form-select>
                </b-form-group>

                <b-form-group
                  id="input-group-Data"
                  label="Sample"
                  label-class="font-size-label font-weight-bold"
                  label-for="input-data"
                >
                  <b-form-textarea
                    id="textarea"
                    v-model="form.input_data"
                    placeholder="Enter the original data..."
                    rows="5"
                  ></b-form-textarea>
                </b-form-group>

                <div class="m-auto align-self-center">
                  <b-button type="submit" variant="primary">Submit</b-button>
                </div>
              </b-form>
            </b-card>

            <b-card title="Transformed Data" class="col-xl-9">
              <b-card v-if="this.trans_state == 'loading'">
                <div class="text-center">
                  <p>Loading data...</p>
                  <b-spinner
                    variant="primary"
                    label="Text Centered"
                  ></b-spinner>
                </div>
              </b-card>
              <b-card v-if="this.trans_state == 'fail'">
                <div class="text-center">
                  <p>
                    Fail to do the transformation, please check the data format
                  </p>
                </div>
              </b-card>
              <b-card v-if="this.trans_state == 'start'">
                <div class="text-center">
                  <p>
                    Click submit to start the transformation<br />Finish form
                    left, try TextFlint fast.
                  </p>
                </div>
              </b-card>
              <b-card v-if="this.trans_state == 'empty'">
                <div class="text-center">
                  <p>
                    There is no element in the input that can be transformed,
                    please change the input or choose another transformation.
                  </p>
                </div>
              </b-card>
              <b-card v-if="this.trans_state == 'busy'">
                <div class="text-center">
                  <p>Server is busy, please try again.</p>
                </div>
              </b-card>
              <b-card v-if="this.trans_state == 'error'">
                <div class="text-center">
                  <p>Server error.</p>
                </div>
              </b-card>

              <b-card
                no-body
                v-for="(sample, sample_index) in this.trans_dataset"
                v-bind:key="sample_index"
                :title="'Result ' + sample_index"
              >
                <b-card-body class="pb-3 pt-3">
                  <b-card-title class="mt-5"
                    >Result {{ sample_index }}</b-card-title
                  >

                  <div>
                    <p
                      class="font-size-data"
                      v-bind:key="sample_index + '_ori'"
                      v-html="'<strong>ori:</strong> ' + sample.ori"
                    ></p>
                    <p
                      class="font-size-data"
                      v-bind:key="sample_index + '_trans'"
                      v-html="'<strong>trans: </strong>' + sample.trans"
                    ></p>
                  </div>
                </b-card-body>
              </b-card>
            </b-card>
          </div>
        </b-tab>
      </b-tabs>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";
export default {
  name: "builder",
  components: {},

  created() {},

  data() {
    // var trans_data_json = require("../../../public/Tasks/Ocr.json");
    // var ori_data_json = require("../../../public/Tasks/ori.json");
    var task_info = require("../../../public/Tasks/SA/task_description.json");
    var demo_samples = require("../../../public/Tasks/demo_samples.json");
    var availiable_trans = task_info["available_domain"].concat(
      task_info["available_ut"]
    );
    return {
      form: {
        task: "SA",
        transformation: "Ocr",
        dataset: null,
        // input_data: ori_data_json,
        input_data: demo_samples["SA"],
      },
      tasks: [
        "ABSA",
        "COREF",
        "DP",
        "MRC",
        "NER",
        "NLI",
        "POS",
        "RE",
        "SA",
        "SM",
        "CWS",
      ],
      transformations: availiable_trans,
      datasets: [],
      show: true,
      trans_dataset: [],
      trans_state: "start",
      demo_samples: demo_samples,
      pathUrl: "",
      dialog: false,
      dialog_video: false,
      video_title: "",
      slides_title: "",
      video_url: "",
    };
  },
  watch: {},
  computed: {
    ...mapGetters(["layoutConfig"]),
    loaderLogo() {
      return process.env.BASE_URL + this.layoutConfig("loader.logo");
    },
  },
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [{ title: "TUTORIALS" }]);
  },
  destroyed() {
    localStorage.removeItem("builderTab");
  },
  methods: {
    show_slides(slides_name) {
      this.pathUrl =
        "https://view.officeapps.live.com/op/view.aspx?src=" +
        encodeURIComponent(
          "https://www.textflint.com/static/media/Slides/" + slides_name
        );
      this.dialog = true;
      this.slides_title = slides_name;
    },
    show_video(video_name) {
      this.video_url =
        "https://www.textflint.com/static/media/Videos/" + video_name;
      this.dialog_video = true;
      this.video_title = video_name;
    },
    /**
     * Reset config
     */
    onSubmit(evt) {
      evt.preventDefault();
      this.trans_state = "loading";
      this.trans_dataset = [];
      if (typeof this.form.input_data == "string") {
        try {
          this.form.input_data = JSON.parse(this.form.input_data);
        } catch {
          this.trans_state = "fail";
          return;
        }
      }

      // var form_data = qs.stringify(this.form)
      this.$axios
        .post("https://www.textflint.com/demo_data", this.form, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((response) => {
          if (response.state == 500) {
            this.trans_state = "error";
          }
          if (response.data) {
            this.trans_state = "";
            this.trans_dataset = response.data.result;
            this.trans_state = response.data.state;
            if (this.trans_dataset.length == 0) {
              this.trans_state = "empty";
            }
          }
        })
        .catch(() => {
          this.trans_state = "busy";
          // alert("Fail to request");
        });
      // alert(JSON.stringify(this.form));
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.task = null;
      this.form.transformation = null;
      this.form.dataset = null;
      this.form.checked = [];
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
    reset(event) {
      event.preventDefault();
      // remove existing saved config
      localStorage.removeItem("config");
      window.location.reload();
    },
    getTransformation() {
      var task_info = require("../../../public/Tasks/" +
        this.form.task +
        "/task_description.json");

      var availiable_trans = task_info["available_domain"].concat(
        task_info["available_ut"]
      );
      this.transformations = availiable_trans;
      this.form.transformation = this.transformations[0];
      if (this.demo_samples[this.form.task] != undefined) {
        this.form.input_data = this.demo_samples[this.form.task];
      } else {
        this.form.input_data = "";
      }
    },

    /**
     * Check if the page loader is enabled
     * @returns {boolean}
     */
    loaderEnabled() {
      return !/false/.test(this.layoutConfig("loader.type"));
    },
    /**
     * Set previous tab active
     */
    setActivePreviousTab() {
      this.tabIndex = parseInt(localStorage.getItem("builderTab")) || 0;
      const links = this.$refs["builder-tab"].querySelectorAll(".nav-link");
      // remove active tab links
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
      }
      this.$refs["builder-tab"]
        .querySelector(`[data-tab="${this.tabIndex}"]`)
        .classList.add("active");
    },
    /**
     * Set current active on click
     * @param event
     */
    setActiveTab(event) {
      const tab = event.target.closest('[role="tablist"]');
      const links = tab.querySelectorAll(".nav-link");
      // remove active tab links
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
      }

      // set clicked tab index to bootstrap tab
      this.tabIndex = parseInt(event.target.getAttribute("data-tab"));

      // set current active tab
      event.target.classList.add("active");

      // keep active tab
      localStorage.setItem("builderTab", this.tabIndex);
    },
    /**
     * Set current active on click
     * @param event
     */
    setResultsTab() {
      // this.setActiveTab(event)
      this.show_component = "WidgetResults";
    },
  },
};
</script>

<style>
.highlight_ori {
  color: rgba(0, 143, 251, 0.85);
}
.highlight_trans {
  color: red;
}
.font-size-data {
  font-size: 1.2rem;
}
.font-size-label {
  font-size: 1.5rem;
}
</style>
